

import React, { useState } from "react";
import { useLocalStorage } from "./useLocalStorage";




function useTodos() {

    const {
        item: todos,
        saveItem: saveTodos,
        loading,
        error,
      } = useLocalStorage('TODOS_V1',[]);
      const [searchValue, setSearchValue] = React.useState('');
      const [openModal, setOpenModal] = React.useState(false);
      const [openModalPrint, setOpenModalPrint] = React.useState(false);
      const [ordenar,setOrdenar] = useState('hoy');
      
      const dias = [
        'Domingo',
        'Lunes',
        'Martes',
        'Miercoles',
        'Jueves',
        'Viernes',
        'Sabado',
    ]
     
     
      
      const hoy= new Date()
          

      const mm= hoy.getMonth() + 1
      const mes= mm < 10 ? '0' + mm : mm;      
      const dd= hoy.getDate()
      const dia= dd < 10 ? '0' + dd : dd;
      const ano= hoy.getFullYear()
      
      
      var fecha1 = new Date()
      fecha1.setDate(fecha1.getDate() + 7);
      const mmm= fecha1.getMonth() + 1
      const mes2= mmm < 10 ? '0' + mmm : mmm;      
      const ddd= fecha1.getDate()
      const dia2= ddd < 10 ? '0' + ddd : ddd;
      const ano2= fecha1.getFullYear()
      
      

     
      const fechaHoy= ano.toString()+ mes.toString()+dia.toString()
      const fechasiete= ano2.toString()+mes2.toString()+dia2.toString()
      
      const fechaHoy2= dia.toString()+'/'+ mes.toString()+'/'+ ano.toString() 
      const fechaHoy4= dia2.toString()+'/'+ mes2.toString()+'/'+ ano2.toString() 
      
      const fechahoynum= Number(fechaHoy)-100
     
      
      const fechafin = Number(fechaHoy)+6
      
     
      const anoS= ''+ ano
      const mesS= ''+ mes
      const diaS= ''+ dia
      
      const diaSemana = hoy.getDay();
      
      const nombreDia = dias[diaSemana];       
      const fechadesde2 = new Date(anoS,mesS-1, diaS).getTime();

      var fechaSemana = new Date();
      var dia7 = 7; // Nmero de das a agregar
      fechaSemana.setDate(fechaSemana.getDate() + dia7);
      console.info('fechaaaaaa ',fechaSemana)
     
      
    
      let searchedTodos = [];

      const completedTodos= todos.filter(todo => !!todo.completed).length;
      const totalTodos = todos.length;
      const futuro = todos.filter(todo => todo.fechaDate > fechaHoy).length;
      const pasado = todos.filter(todo => todo.fechaDate < fechaHoy-5).length;
      const paradas = todos.filter(todo => todo.stop).length;
     
      if(!searchValue.length >= 1) {
        
      switch (ordenar){
        case 'tarea':
          
          searchedTodos = todos.sort((a, b) => (a.text > b.text ? 1 : a.text < b.text ? -1 : 0))
          
          break
        case 'prioridad':
          searchedTodos = todos.sort((a,b) => a.prioridad - b.prioridad)
          break
        case 'fecha':
          searchedTodos = todos.sort((a,b) => a.fechaDate -  b.fechaDate)
          
          searchedTodos = todos.filter(todo => {
            const todoantes = (todo.fechaDate <= fechaHoy)             
            return todoantes
          })                
          break  

        case 'semanal':     
        searchedTodos = todos.sort((a,b) => a.fechaDate -  b.fechaDate)
          
        
          
          searchedTodos = todos.filter(todo => {
            
            const todoantes = (todo.fechaDate >= fechaHoy && todo.fechaDate <= fechafin)             
            return todoantes
          })                
          break

        case 'futuras':
          
          searchedTodos = todos.filter(todo => {
           
            const todofuturas = (todo.fechaDate > fechaHoy) 
            
            return todofuturas
          })                
          searchedTodos = searchedTodos.sort((a,b) => a.fechaDate -  b.fechaDate)
          break
        case 'hoy':
          searchedTodos = todos.filter(todo => {
                 
            const todofuturas = (todo.fechaDate === fechaHoy)
             
            return todofuturas
        })
        break

        case 'limpiar':          
          searchedTodos = todos.filter(todo => {
           
            todo.completed = false; 
           
            const todofuturas = (todo.completed)
            return todofuturas
        })
        break

        case 'paradas':
          searchedTodos = todos.sort((a,b) => a.fechaDate -  b.fechaDate)
          searchedTodos = todos.filter(todo => {
           
            const todofuturas = (todo.stop)
            return todofuturas
        })
        break

        case 'pendientes':          
          searchedTodos = todos.filter(todo => {
            searchedTodos = todos.sort((a,b) => a.fechaDate -  b.fechaDate)  
            const todofuturas = ((!todo.completed && !todo.stop) && todo.fechaDate <= fechaHoy)           
            return todofuturas
        })
          break

          case 'vistas':
            searchedTodos = todos.sort((a,b) => a.fechaDate -  b.fechaDate)
            searchedTodos = todos.filter(todo => {
              const todofuturas = (todo.completed)
              return todofuturas
            })
              break  

        case 'vencidas':
          searchedTodos = todos.sort((a,b) => a.fechaDate -  b.fechaDate)
          searchedTodos = todos.filter(todo => {
            const todofuturas = (todo.fechaDate < fechahoynum)
            return todofuturas
          })
            break

        case 'urgentes':
          searchedTodos = todos.sort((a,b) => a.fechaDate -  b.fechaDate)
          searchedTodos = todos.filter(todo => {
            const todofuturas = (todo.urgente)
            return todofuturas
              })
                break
        default:
          searchedTodos = todos
          
      }
         
        
      } else {
          searchedTodos = todos.filter(todo => {
            const todoText = todo.text.toLowerCase()
            const searchText = searchValue.toLowerCase()
            
            return todoText.includes(searchText)
          })
          
      }
      
      const addTodo =(text,fechaSeleccionada,prioridad,nota,fechaDate,diaSemana,fechadesde) => {  
        console.log('addTodo:', todos)     
        const newTodos =[...todos];
        newTodos.push({
          completed: false,
          text,
          fechaSeleccionada,
          prioridad,
          nota,
          fechaDate,
          diaSemana,
          fechadesde,
          urgente: false,
          stop:false,
        })

        console.log('newTodos: ',newTodos)
        saveTodos(newTodos);    
      }

      const completeTodo =(text) => {
        const todoIndex = todos.findIndex(todo => todo.text === text);
    
        const newTodos =[...todos];

        if(newTodos[todoIndex].completed === false){
            newTodos[todoIndex].completed = true;
            newTodos[todoIndex].urgente = false;
            newTodos[todoIndex].stop = false;       
          } else {
              newTodos[todoIndex].completed = false;        
      }
        saveTodos(newTodos);    
      }

      const stopTodo =(text) => {
        const todoIndex = todos.findIndex(todo => todo.text === text);
    
        const newTodos =[...todos];

        if(newTodos[todoIndex].stop === false){
            newTodos[todoIndex].stop = true;
            newTodos[todoIndex].urgente = false; 
            newTodos[todoIndex].completed = false; 
          } else {
              newTodos[todoIndex].stop = false;        
      }
        saveTodos(newTodos);    
      }

      const calendarTodo =(text) => {
        const todoIndex = todos.findIndex(todo => todo.text === text);
    
        const newTodos =[...todos];

        newTodos[todoIndex].fechaSeleccionada = fechaHoy2;
        newTodos[todoIndex].diaSemana = nombreDia;
        newTodos[todoIndex].fechadesde = fechadesde2;

        saveTodos(newTodos);    
      }

      const calendarTodo7 =(text) => {
        const todoIndex = todos.findIndex(todo => todo.text === text);
    
        const newTodos =[...todos];

        newTodos[todoIndex].fechaSeleccionada = fechaHoy4;
        newTodos[todoIndex].diaSemana = nombreDia;
        newTodos[todoIndex].fechadesde = fechadesde2;
        newTodos[todoIndex].fechaDate = fechasiete;
        saveTodos(newTodos);    
      }
     
    
      const deleteTodo =(text) => {
        const todoIndex = todos.findIndex(todo => todo.text === text);
    
        const newTodos =[...todos];
    
        newTodos.splice(todoIndex,1);
        saveTodos(newTodos);    
      }

          
      const urgenteTodo =(text) => {
        const todoIndex = todos.findIndex(todo => todo.text === text);
    
        const newTodos =[...todos];

        if(newTodos[todoIndex].urgente === false){
            newTodos[todoIndex].urgente = true;
            newTodos[todoIndex].completed = false;
            newTodos[todoIndex].stop = false;
          } else {
              newTodos[todoIndex].urgente = false;        
      }
        saveTodos(newTodos);    
      }
    
      React.useEffect(() => {
       
      },[totalTodos])
    
      

    return {
            error,
            loading,
            totalTodos,
            completedTodos,
            searchValue,
            setSearchValue,
            searchedTodos,
            completeTodo,
            deleteTodo, 
            stopTodo,
            calendarTodo,
            calendarTodo7,
            urgenteTodo,          
            openModal,
            setOpenModal,
            openModalPrint,
            setOpenModalPrint,
            addTodo,
            setOrdenar,
            ordenar,
            futuro,
            pasado,
            paradas,
        }
}

export { useTodos };